import React from 'react';

class Navigation extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            links: []
        }

        // First entry should be the home page link
        for(var i=0; i<this.props.pages.length; i++) {
            this.addNavbarEntry(this.props.pages[i].linkText, 
                this.props.pages[i].activeLink, 
                this.props.pages[i].linkTo);
        }
    }

    addNavbarEntry(linkText, activeLink, linkTo) {
        if(activeLink) {
            this.setState(prevState => ({
                links: [...prevState.links, (
                    <li className="active"><a href="{ linkTo }">{linkText}</a></li>
                )]
            }))
        } else {
            this.setState(prevState => ({
                links: [...prevState.links, (
                    <li><a href="{ linkTo }">{linkText}</a></li>
                )]
            }))
        }
    }

    render() {
        return (
            <nav>
                <div className="nav-wrapper">
                    <a href="{ this.props.pages.home.linkTo }" className="brand-logo center">UMEET</a>
                    <ul className="left hide-on-med-and-down">
                        {this.state.links}
                    </ul>
                </div>
            </nav>
        )
    }
}

export default Navigation;